import React, {useState, useEffect} from 'react'
import {motion} from "framer-motion"

function Btn({value, type}) {
    const [state, setstate] = useState("btn")
    useEffect(() => {
        if(type === "secoundary"){
            setstate("btn secoundary")
        }else if(type === "primary"){
            setstate("btn primary")
        }
    }, [])
    return (
        <motion.div whileTap={{scale: .8}} className={state}>
            <div className="in">
                {value}
            </div>
        </motion.div>
    )
}

export default Btn
