import React, { Component } from 'react'
import Btn from './common/Button'
import Btn2 from './common/Button2'



export class Calc extends Component {
    state = {
        btns: [
            { value: "C", type:"primary" },
            { value: "+/-", type:"primary" },
            { value: "%", type:"primary" },
            { value: "/", type:"secoundary" },
            { value: 7, type:"num" },
            { value: 8, type:"num" },
            { value: 9, type:"num" },
            { value: "*", type:"secoundary" },
            { value: 4, type:"num" },
            { value: 5, type:"num" },
            { value: 6, type:"num" },
            { value: "-", type:"secoundary" },
            { value: 1, type:"num" },
            { value: 2, type:"num" },
            { value: 3, type:"num" },
            { value: "+", type:"secoundary" },
            { value: 0, type:"num" },
            { value: 0, type:"num" },
            { value: ".", type:"num" },
            { value: "=", type:"secoundary" },
        ]
    }
    render() {
        return (
            <div className="calc">
                <div className="result">20,810</div>
                <div className="btns">
                    {this.state.btns.map(item => <Btn key={item.value} value={item.value} type={item.type} />)}
                </div>
            </div>
        )
    }
}


